// Accordion
// ---------------------------
.accordion {
    @include rem('margin-bottom', 21px);
    @include clearfix();
    clear: both;
    
    &__item {
        @include rem('margin', 0 0 14px);
    
        &__header {
            margin: 0;
            @include SourceSansProRegular();
            @include rem('font-size', 24px);
            line-height: (28px / 24px);

            a {
                position: relative;
                display: block;
                color: $black;
                @include rem('padding', 6px 36px 6px 7px);
                @include rem('min-height', 24px);
                @include rem('border-bottom', 1px solid $zombie);
                @include transition($property: background-color, $duration: 100ms);

                &:focus {
                    border-bottom-color: transparent;
                }

                &:hover,
                &:active {
                    color: $black;
                    background-color: $rum_swizzle;
                    text-decoration: none;
                }

                .arrow-icon {
                    @include single-arrow-icon($direction: 'down', $size: 'large', $color: $link_color);
                    position: absolute;
                    @include rem('top', 22px);
                    @include rem('right', 9px);
                }
            }
        }
        &__content {
            display: none;
            background-color: $bianca;
            @include rem('padding', 21px 15px 7px);
            @include rem('min-height', 42px);
            border-top: 0 none;
            
            h3 {
                @include rem('font-size', 22px);
                line-height: ( 28px / 22px );
                @include SourceSansProSemibold();
            }

            p + ul,
            p + ol {
                @include rem('margin-top', -21px);
            }

            @media (min-width: $md_break) {
                @include rem('padding', 21px 18px);
            }
        }
    }
}

// Toggle arrow on active
.accordion__item.active .arrow-icon {
    @include single-arrow-icon($direction: 'up', $size: 'large', $color: $link_color);
    position: absolute;
    @include rem('top', 22px);
    @include rem('right', 9px);
}

// Toggled accordion
.accordion__item.active .accordion__item__content {
    display: inherit;
}