.notice {
    position: relative;
    @include rem('padding', 63px 21px 14px);
    @include border-radius();
    @include box-sizing($boxmodel: content-box);
    // Default style
    @include rem('border', 1px solid $silver);
    background-color: $white_sand;
  
    .grunt-icon {
        position: absolute;
        @include rem('top', 20px);
        @include rem('left', 22px);
        // Small icon
        @include rem('width', 32px);
        @include rem('height', 32px);

        svg {
            display: block;
            width: 100%;
            height: 100%;
        }
    }
    
    .icon-warning--triangle {
        @include rem('left', 12px);
        @include rem('width', 64px);
    }

    &--red {
        @include rem('border', 1px solid $brand_danger);
        background-color: $white;
        
        @media (max-width: $md_break_max) {
            .icon-warning--triangle svg .symbol {
                fill: $brand_danger;
            }
            .icon-warning--triangle svg .triangle {
                fill: $brand_danger;
            }
        }
    }


    &--green {
        @include rem('border', 1px solid $brand_success);
        background-color: rgba($brand_success, 3/100);
    }
    
    &--blue {
        @include rem('border', 1px solid $spindle);
        background-color: $water;

        .icon-warning--circle svg .symbol {
            fill: $link_color;
        }
        .icon-warning--circle svg .circle {
            fill: $danube;
        }
    }

    &__content {
        @include clearfix();
        @include rem('max-width', 600px);

        h1, h2, h3, h4, h5, h6 {
            margin-top: 0;
        }

        a {
            text-decoration: underline;
        }
    }

    @media (min-width: $md_break) {
        padding-bottom: 0;
        @include rem('padding-top', 21px);
        @include rem('padding-left', 116px);
        @include rem('min-height', 88px);
        margin-top: 0;

        .grunt-icon {
            @include rem('top', 23px);
            @include rem('left', 25px);
            // Large icon
            @include rem('width', 64px);
            @include rem('height', 64px);
        }
    
        .icon-warning--triangle {
            @include rem('left', 12px);
            @include rem('width', 96px);
        }
    }
    
        
    &.notice--small {
        min-height: inherit;
        @include rem('margin-top', 21px);
        @include rem('padding', 21px);

        .icon-notice {
            @include rem('top', -16px);
            @include rem('left', 7px);
            // Small icon
            @include rem('width', 32px);
            @include rem('height', 32px);
            background-color: $water;
            @include border-radius(50%);
        }
    }
}


