.contact-module {
    @include clearfix();

    &__map-container {
        @include rem('height', 190px);
        position: relative;
    }

    &__map {
        @include rem('margin-bottom', 14px);
        position: absolute;
        z-index: 1;
        top: 0;
        left: 0;
        height: 100%;
        width: 100%;
        background-position: center center;
        background-size: cover;
        background-color: $janna;
        @include border-radius($base_border_radius $base_border_radius 0 0);

        &--desktop {
            display: none;
        }
    }

    &__link {
        position: absolute;
        z-index: 2;
        top: 0;
        left: 0;
        height: 100%;
        width: 100%;

        &:hover .contact-module__link-text,
        &:active .contact-module__link-text,
        &:focus .contact-module__link-text {
            text-decoration: underline;
            background-color: $white;
            @include rem-box-shadow(0 1px 2px rgba($black, 18/100));
        }

        .contact-module__link-text {
            position: absolute;
            z-index: 3;
            top: 0;
            @include rem('left', 22px);
            @include rem('padding', 9px 32px 9px 12px);
            @include rem('font-size', 16px);
            line-height: (21px / 16px);
            background-color: rgba($white, 97/100);
            @include border-radius(0 0 $base_border_radius $base_border_radius);
            @include rem-box-shadow(0 1px 2px rgba($black, 8/100));
        }
        
        &.external_link:after {
            position: absolute;
            @include rem('top', 14px);
            @include rem('left', 130px);
            z-index: 4;
        }
    }

    &__contact {
        @include rem('margin-top', 20px);
    }

    &__contact-content {
        @include hyphens($mode: none);
        @include clearfix();
        
        .with_icon .grunt-icon {
            @include rem('top', -5px);
            @include rem('left', -6px);
        }
    }

    @media (min-width: $md_break){
        .contact-module__map-container{
            @include rem('height', 378px);
        }
    }

    @media (min-width: $lg_break) {

        .contact-module__map--mobile {
            display: none;
        }

        .contact-module__map--desktop {
            display: inherit;
        }


    }

}

.contact-information-person {
    display: flex;
    justify-content: space-between;
    width: 100%;

    &__info-list {
        flex: 1;
        min-width: 10px;
        word-break: keep-all;
    }

    // DT
    &__label {
        display: inline;
        margin: 0;
        @include SourceSansProRegular();

        // screen-reader-text
        &--srt {
            clip: rect(1px, 1px, 1px, 1px);
            height: 1px;
            overflow: hidden;
            position: absolute !important;
            width: 1px;
        }
    }

    // DD
    &__content {
        display: inline;
        @include rem('margin', 7px 0);

        &--with-icon {
            display: block;
            position: relative;
            @include rem('padding-left', 30px);
            
            a {
                text-decoration: none;

                &:hover,
                &:active {
                    text-decoration: underline;
                }
            }

            .grunt-icon {
                position: absolute;
                @include rem('top', -3px);
                @include rem('left', -5px);
            }
        }

        &:after {
            content: '\A';
            white-space: pre;
        }
    }

    &__image img {
        @media only screen and (min-width: $md_break) {
            width: 160px;
        }

        height: auto;
        width: 110px;

    }
}