.search-checkbox-filter {
    display: block;
    padding: 0;
    @include rem('border', 1px solid $zombie);
    @include border-radius($border_radius_large);

    legend {
        width: 100%;
        padding-left: 0;
        padding-right: 0;
        @include rem('margin-bottom', 14px);
    }
    
    &__title {
        display: none;
        @include rem('padding', 0 0 2px 0);
        width: 100%;

        &--advanced {
            position: relative;
            display: block;
            color: $black;
            @include rem('border-bottom', 1px solid $zombie);

            &:hover {
                color: $black;
                text-decoration: none;
            }

            .arrow-icon {
              @include single-arrow-icon($direction: 'down', $size: 'large', $color: $link_color);
              position: absolute;
              @include rem('top', 13px);
              @include rem('right', 10px);
            }
        }
    }
    
    // All content
    &__content {
        display: none;
        @include rem('padding', 0 12px 0 10px);
    }

    // One group of checkboxes
    .search-checkbox-filter__group {
        display: none;
    }

    &__close-button {
        display: none;
    }

    &__button {
        position: relative;
        color: $link_color;
        width: 100%;
        text-align: left;
        border: none;
        margin: 0;
        background-color: $white;
        @include rem('font-size', 21px);
        @include border-radius($border_radius_large);
        
        .arrow-icon {
          @include single-arrow-icon($direction: 'down', $size: 'large', $color: $link_color);
          position: absolute;
          @include rem('top', 20px);
          @include rem('right', 22px);
        }

        &:hover,
        &:active {
            background-color: $white;
            color: $link_color;
        }
    }


    &__reset-link {
        text-decoration: underline;
    }

    // One group of checkboxes + title
    &__fieldset {

        &--open {

            .search-checkbox-filter__group {
                display: block;
            }

            .search-checkbox-filter__group {
                @include rem('border-bottom', 1px solid $zombie);
            }

            .search-checkbox-filter__title--advanced .arrow-icon {
                @include single-arrow-icon($direction: 'up', $size: 'large', $color: $link_color);
                position: absolute;
                @include rem('top', 13px);
                @include rem('right', 10px);
            }
        }
    }

    &--open {
        //For å overstyre mb-klasse når filteret er åpent på mobil
        @include rem('margin-bottom', 56px !important);

        .search-checkbox-filter__button {
            background-color: $rum_swizzle;
            color: $link_color;
            @include rem('border-bottom', 1px solid $zombie);
            border-bottom-right-radius: 0;
            border-bottom-left-radius: 0;
            @include rem('margin-bottom', 14px);

            &:hover,
            &:active {
                background-color: $rum_swizzle;
                color: $link_color;
            }
        }

        .search-checkbox-filter__button .arrow-icon {
            @include single-arrow-icon($direction: 'up', $size: 'large', $color: $link_color);
            position: absolute;
            @include rem('top', 20px);
            @include rem('right', 22px);
        }

        .search-checkbox-filter__content {
            display: block;
        }

        .search-checkbox-filter__close-button {
            display: block;
            text-align: center;
            @include border-radius(0 0 $border_radius_large $border_radius_large);
            @include rem('border', 1px solid $zombie);
            border-top: none;
            background-color: $white;
            position: absolute;
            margin: 0 auto;
            left: 0;
            right: 0;
            @include rem('max-width', 70px);
            @include rem('padding', 7px 17px);
            @include rem('font-size', 17px);
            line-height: ( 21px / 17px );
        }
    }

    @media only screen and (min-width: $lg_break) {
        border: none;

        &__title {
            display: block;
            color: $black;
            width: 100%;
            @include rem('border-bottom', 2px solid $zombie);
        }
        
        &--open .search-checkbox-filter__close-button {
            display: none;
        }

        &__button,
        &__close-button {
            display: none;
        }

        &__content {
            display: block;
            padding: 0;
        }

        &__group {
            display: block;
        }
    }
}

// Liste av barn / Underavdelinger
// -------------------------------------------
.filter-search {
    position: relative;
    @include rem('max-width', 480px);

    &__label {
        position: absolute;
        @include rem('top', 6px);
        @include rem('left', 4px);
        margin: 0;
    }

    &__input {
        width: 100%;
        @include rem('margin', 0 0 14px);
        @include rem('padding-left', 42px);
        @include rem('padding-right', 42px);
        @include rem('border', 1px solid $form_main_color);
    }
    
    &__clear-search-button {
        position: absolute;
        @include rem('top', 7px);
        @include rem('right', 8px);
        padding: 0;
        margin: 0;
        border: 0 none;
        background-color: transparent;

        &:hover,
        &:active {
            background-color: transparent;
        }
    }
}

// Remove default input x
// IE10+
.filter-search .filter-search__input[type=search]::-ms-clear {
    display: none;
}

// Chrome
.filter-search .filter-search__input[type=search]::-webkit-search-decoration,
.filter-search .filter-search__input[type=search]::-webkit-search-cancel-button,
.filter-search .filter-search__input[type=search]::-webkit-search-results-button,
.filter-search .filter-search__input[type=search]::-webkit-search-results-decoration {
    display: none;
}

// Eller vis alle..
.filter_shortcut_container {
    @include rem('margin-top', 16px);
}

.filter-search + .filter_shortcut_container {
    margin-top: 0;
}

// No max width
.filter-search.no_max_width {
    max-width: inherit;
}

// Results
.js_filter_search_result_list .match .list_highlight {
    font-weight: 600;
}

// Eiendomsskatt
// -------------------------------------------
.search-property{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;

    .form-element{
        @include rem('width', 136px);

        @include rem('margin', 0 0 14px 0);
    }

    .form-element:last-of-type{
        @include rem('margin',  0 0 28px 0);
    }

    @media only screen and (min-width: $md_break){
        flex-direction: row;
        align-items: flex-end;

        .form-element{
            @include rem('margin', 0 14px 0 0);
        }

        .form-element:last-of-type{
            @include rem('margin', 0 14px 0 0);
        }
    }
}

.search-property__searchfield{
    position: relative;
}


