.grunt-icon {
    display: block;
    background-size: contain;
    
    // Size change
    // ----------------------------------------
    &--32 {
        @include rem('height', 32px);
        @include rem('width', 32px);
        svg {
            @include rem('height', 32px);
            @include rem('width', 32px);
        }
    }
    &--32-64 {
        @include rem('height', 32px);
        @include rem('width', 64px);
        svg {
            @include rem('height', 32px);
            @include rem('width', 64px);
        }
    }
    &--64-32 {
        @include rem('height', 64px);
        @include rem('width', 32px);
        svg {
            @include rem('height', 64px);
            @include rem('width', 32px);
        }
    }
    &--64 {
        @include rem('height', 64px);
        @include rem('width', 64px);
        svg {
            @include rem('height', 64px);
            @include rem('width', 64px);
        }
    }

    // Color change - only when embed as svg
    // ----------------------------------------
    &--white svg {
        @include grunticon-color($white);
    }
    &--link_color svg {
        @include grunticon-color($link_color);
    }
    &--silver svg {
        @include grunticon-color($silver);
    }
    &--brand_success svg {
        @include grunticon-color($brand_success);
    }
}
